import React from 'react';
import { GOOGLECLIENTID } from '../../environment';
import { Login, LoginSocial, ErroLogin } from '../../utils/Auth';


import GoogleLogin from 'react-google-login';
import { useHistory, useLocation } from "react-router-dom";

import {
    Container,
    Wrapperleft,
    WrapperRight,
    WrapperLogin,
    WrapperSocial,
    WrapperInput,
    WrapperOption,
    WrapperAdd,
    IconGoogle,
    IconLogIn,
    WrapperCadastro
} from './styles';

import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';

import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';

import { TabComponet } from './Tab';


const LoginForm  = ({avancar, useBackdrop, useSnackbarsAlert}) => {

    let history = useHistory();
    let location = useLocation();
    const {openBackdrop, closeBackdrop} = useBackdrop
    const {openSnackbarsAlert, closeSnackbarsAlert} = useSnackbarsAlert
    let { from } = location.state || { from: { pathname: "/" } };
    
    const [input, setInput] = React.useState({
        username: {value: "", error: false, valido: false},
        password: {value: "", error: false, valido: false},
    })

    const  lidandoForm = (event) => {
        if(event.target.value.length > 0){
            setInput({...input, [event.target.name]: {value: event.target.value, error: false, valido: true}})
        }else{
            setInput({...input, [event.target.name]: {value: event.target.value, error: false, valido: true}})
        }
    }

    const __LoginSocial = async (googleResp) => {
        openBackdrop()
        let resp = await  LoginSocial(googleResp)
        if(resp?.result){
            setTimeout(() => {
                closeBackdrop()
                history.replace(from);
            }, 300);
       }else{
            setTimeout(() => {
                closeBackdrop()
                openSnackbarsAlert({
                    msg: resp?.err,
                    severity: "error"
                })
        }, 300);
       }
    }

    const __Login = async () => {
        openBackdrop()
       let resp = await Login(input.username.value, input.password.value)
       if(resp?.result){
            setTimeout(() => {
                closeBackdrop()
                history.replace(from);
            }, 300);
       }else{
        setTimeout(() => {
            closeBackdrop()
            openSnackbarsAlert({
                msg: resp?.err,
                severity: "error"
            })
        }, 300);
       }
    }

    return (
        <WrapperLogin>
                            
        <WrapperSocial >
            
            <GoogleLogin
                clientId={GOOGLECLIENTID}
                buttonText="Login"
                onSuccess={__LoginSocial}
                onFailure={ErroLogin}
                cookiePolicy={'single_host_origin'}
                render={renderProps => (
                    <Button
                    variant="contained"
                    color="secondary"
                    startIcon={<IconGoogle />}
                    onClick={renderProps.onClick} 
                    disabled={renderProps.disabled}
                    style={{ width: '100%' }}
                >
                        Google
                </Button>
                )}
            />

        </WrapperSocial>
        <Divider />
        <WrapperInput >         
            <TextField
                id="descricao-nome"
                label="Email"
                style={{ margin: '5px 0 10px 0' }}
                placeholder=""
                helperText="Email cadastrado"
                fullWidth
                margin="normal"
                variant="outlined"
                InputLabelProps={{
                    shrink: true,
                }}
                name="username"
                value={input.username.value}
                onChange={lidandoForm}
            />
            <TextField
                    id="descricao-id"
                    label="Senha"
                    style={{ margin:  '5px 0 0 0' }}
                    type="password"
                    helperText="Senha cadastrado"
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                        shrink: true,
                    }}
                    name="password"
                    value={input.password.value}
                    onChange={lidandoForm}
            />
            <WrapperOption >
                <WrapperAdd >
                    <Button 
                        startIcon={<IconLogIn/>}
                        variant="contained" 
                        color="primary" 
                        style={{ width: '100%' }}
                        onClick={avancar} 
                        >
                    
                        CRIAR CONTA 
                    </Button>
                </WrapperAdd>
                <WrapperAdd >
                    <Typography variant="overline" display="block" gutterBottom  style={{ fontSize: '10px' }} >
                        Esqueceu a senha ?
                    </Typography>
                </WrapperAdd>
            </WrapperOption>
            <WrapperCadastro >
                    <Button 
                        startIcon={<IconLogIn/>}
                        variant="contained" 
                        color="primary" 
                        style={{ width: '100%' }}
                        onClick={__Login}
                        disabled={input.username.valido && input.password.valido ? false : true} 
                        >
                        ENTRAR 
                    </Button>
            </WrapperCadastro>
        </WrapperInput>
        </WrapperLogin>
    )

}

export { LoginForm }