const LISTADEMSG = {
    nome: { 
            sucesso: "", 
            erro: "Nome não é valido"
        },
    email: { 
            sucesso: "", 
            erro: "E-mail não é valido"
        },
    senha: { 
            sucesso: "", 
            erro: "Senha não é valida"
        },
    senhaConfirma: {
        sucesso: "", 
        erro: "Senha não coincide"        
    }
}

function text(value, msg=false){
    if (value){
        if(value.length > 2 ){
            return {status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : "" }
        }else{
            return {status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : "" }
        }
    }
    return {status: true, msg: "" }
}

function email(value, msg=false){
    let mailformat = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    if (value){
        if( value.match(mailformat) ){
            return {status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        }
    }
    return {status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : "" }
}

function senha(value, msg=false){
    
    //A string deve conter pelo menos 1 caractere alfabético minúsculo
    //A string deve conter pelo menos 1 caractere alfabético maiúsculo
    //A string deve conter pelo menos 1 caractere numérico
    //A string deve conter pelo menos um caractere especial,
    //A string deve ter oito caracteres ou mais
    if(value){
        var strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})");
        var mediumRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})");
    
        if(strongRegex.test(value)) {
            return {status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        } else if(mediumRegex.test(value)) {
            return {status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        } else {
            return {status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : "" }
        }
    }
    return {status: true, msg: ""}

}

function senhaConfirma(senha, value, msg=false){
    
    if(value){
        if(senha === value){
            return {status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        }else{
            return {status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : ""}
        }
    }
    return {status: true, msg: ""}

}

const Valida = {
    text: text,
    email: email,
    senha: senha,
    senhaConfirma: senhaConfirma
}

export { Valida } 