import React from 'react';
import { GOOGLECLIENTID } from '../../environment';
import { Login, ErroLogin } from '../../utils/Auth';

import GoogleLogin from 'react-google-login';
import { useHistory, useLocation } from "react-router-dom";

import {
    Container,
    Wrapperleft,
    WrapperRight,
    WrapperLogin,
    WrapperSocial,
    WrapperInput,
    WrapperOption,
    WrapperAdd,
    IconGoogle,
    IconLogIn,
    WrapperCadastro
} from './styles';

import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';

import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';

import { TabComponet } from './Tab';

import { Valida } from '../../utils/Validando';
import { envia } from '../../utils/Services';

const CadastroForm  = ({avancar, useBackdrop, useSnackbarsAlert}) => {

    let history = useHistory();
    let location = useLocation();
    const {openBackdrop, closeBackdrop} = useBackdrop
    const {openSnackbarsAlert, closeSnackbarsAlert} = useSnackbarsAlert
    let { from } = location.state || { from: { pathname: "/" } };

    const [input, setInput] = React.useState({
        nome: {value: "", error: false, valido: false},
        email: {value: "", error: false, valido: false},
        senha: {value: "", error: false, valido: false},
        senhaConfirma: {value: "", error: false, valido: false},
    })

    const  lidandoForm = (event, validate) => {
        let valida  = Valida[validate]
        if(valida){
            let result = valida(event.target.value, event.target.name)
            if (result.status){
                setInput({...input, [event.target.name]: {value: event.target.value, error: false, valido: true}})
            }else{
                setInput({...input, [event.target.name]: {value: event.target.value, error: result.msg, valido: false}})
            }
        }
    }

    const  lidandoFormSenha = (event, validate) => {
        let valida  = Valida[validate]
        if(valida){
            let result = valida(input.senha.value, event.target.value, event.target.name)
            if (result.status){
                setInput({...input, [event.target.name]: {value: event.target.value, error: false, valido: true}})
            }else{
                setInput({...input, [event.target.name]: {value: event.target.value, error: result.msg, valido: false}})
            }
        }
    }

    const __Login = async (username, password) => {
        let resp = await Login(username, password)
        if(resp?.result){
                setTimeout(() => {
                    closeBackdrop()
                    history.replace(from);
                }, 300);
        }else{
            setTimeout(() => {
                closeBackdrop()
                openSnackbarsAlert({
                    msg: resp?.err,
                    severity: "error"
                })
            }, 300);
        }
    }

    async function enviaForm(){
        openBackdrop()
        let novoForm = {
                username: input.email.value, 
                first_name: input.nome.value, 
                email: input.email.value,
                password: input.senha.value,
            }
        let result = await envia('account_register', novoForm)
        if (result?.id) {
            await __Login(novoForm.username, novoForm.password)
        }else{
            setTimeout(() => {
                closeBackdrop()
                openSnackbarsAlert({
                    msg: "",
                    severity: "error"
                })
            }, 300);
        }
    }


    return (
        <WrapperLogin>

        <WrapperSocial ></WrapperSocial>
        <WrapperInput >

            <TextField
                id="form-nome"
                label="Nome"
                style={{ margin: '5px 0 10px 0' }}
                placeholder=""
                helperText="Nome do usuário"
                fullWidth
                margin="normal"
                variant="outlined"
                InputLabelProps={{
                    shrink: true,
                }}
                value={input.nome.value}
                name="nome"
                onChange={ (event) => lidandoForm(event, 'text') }
                helperText={input.nome.error ? input.nome.error : ""}
                error={input.nome.error ? true : false}
            />

            <TextField
                id="form-email"
                label="E-mail"
                style={{ margin: '5px 0 10px 0' }}
                placeholder=""
                fullWidth
                margin="normal"
                variant="outlined"
                InputLabelProps={{
                    shrink: true,
                }}

                value={input.email.value}
                name="email"
                onChange={ (event) => lidandoForm(event, 'email') }
                helperText={input.email.error ? input.email.error : ""}
                error={input.email.error ? true : false}
            />
            <TextField
                    id="from-senha"
                    label="Senha"
                    style={{ margin:  '5px 0 10px 0' }}
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                        shrink: true,
                    }}

                    type="password"
                    value={input.senha.value}
                    name="senha"
                    onChange={ (event) => lidandoForm(event, 'senha') }
                    helperText={input.senha.error ? input.senha.error : ""}
                    error={input.senha.error ? true : false}
            />

            <TextField
                    id="form-senha-confirma"
                    label="Confirme a senha"
                    style={{ margin:  '5px 0 10px 0' }}
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    InputLabelProps={{
                        shrink: true,
                    }}

                    type="password"
                    value={input.senhaConfirma.value}
                    name="senhaConfirma"
                    onChange={ (event) => lidandoFormSenha(event, 'senhaConfirma') }
                    helperText={input.senhaConfirma.error ? input.senhaConfirma.error : ""}
                    error={input.senhaConfirma.error ? true : false}
            />

            <WrapperOption >
                <WrapperAdd >
                    <Button 
                        startIcon={<IconLogIn/>}
                        variant="contained" 
                        color="primary" 
                        style={{ width: '90%' }}
                        onClick={avancar} 
                        >
                        VOLTAR
                    </Button>
                </WrapperAdd>
                <WrapperAdd >
                    {
                        input.nome.valido && input.email.valido && input.senha.valido && input.senhaConfirma.valido ?
                        <Button 
                            startIcon={<IconLogIn/>}
                            variant="contained" 
                            color="primary" 
                            style={{ width: '90%' }}
                            onClick={enviaForm} 
                            >
                            CRIAR CONTA  
                        </Button>
                        :
                        <Button 
                            startIcon={<IconLogIn/>}
                            variant="contained" 
                            color="primary" 
                            style={{ width: '90%' }}
                            disabled={true}
                            >
                            CRIAR CONTA  
                        </Button>
                    }
                </WrapperAdd>
            </WrapperOption>
        </WrapperInput>
        </WrapperLogin>
    )

}

export { CadastroForm }