import React from 'react';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListSubheader from '@material-ui/core/ListSubheader';
import Switch from '@material-ui/core/Switch';
import WifiIcon from '@material-ui/icons/Wifi';
import BluetoothIcon from '@material-ui/icons/Bluetooth';

import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import Icon from '@material-ui/core/Icon';

import Avatar from '@material-ui/core/Avatar';

import SubjectIcon from '@material-ui/icons/Subject';
import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';

import {
    Accordion,
    AccordionSummary,
    AccordionDetails,
    ListItemText,
    ListItemAvatar,
  } from '@material-ui/core';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import EditIcon from '@material-ui/icons/Edit';

import { 
    Container, 
    Main,
} from '../../styles/Layout';

import { 
    WrapperButton,
    useStyles,
} from './styles';

import moment from 'moment';
import Modal from '@material-ui/core/Modal';

import { Edicao } from './Edicao';

function Solicitacao() {

    
    const [eventos, setEventos, EventosInicial, addEventos, rmEventos, putEventos, upEventos] = useGenericModel('account_events');
    const [solicitado, setSolicitado ] = React.useState([]);
    const classes = useStyles();

    const [openALert, closeAlert, Alert] = useSnackbarsAlert()
    const [openBackdro, closeBackdro, Backdro] = useBackdrop()
    
    React.useEffect(() => {
        if(eventos){
            console.log(eventos)
            setSolicitado([...eventos.filter( evento =>  evento.status === 0 )])
        }
    },[eventos])

    const handlerConcluir = async (evento) => {
        openBackdro()

        let novoEvento = {...evento}
        delete novoEvento['cliente']
        delete novoEvento['servico']
        novoEvento.status = 3

        let result = await putEventos(novoEvento, `${novoEvento.id}/`)
        if ( result.hasOwnProperty("id") ) {
            openALert({
                msg: "Solicitação confirmada!",
                severity: "success"
            })
        }else{
            openALert({
                msg: "Solicitação não pode ser concluida",
                severity: "error"
            })
        }
        closeBackdro()
    }

    const handlerEditar  = async (evento, dia, handleClose) => {
        handleClose()
        setTimeout( async () => {
            openBackdro()
            let novoEvento = {...evento}
            delete novoEvento['cliente']
            delete novoEvento['servico']
            novoEvento['dia'] = dia
    
            let result = await putEventos(novoEvento, `${novoEvento.id}/`)
            if ( result.hasOwnProperty("id") ) {
                openALert({
                    msg: "Solicitação confirmada!",
                    severity: "success"
                })
            }else{
                openALert({
                    msg: "Solicitação não pode ser concluida",
                    severity: "error"
                })
            }
            closeBackdro()
        }, 300);
    }


  return (
    <List subheader={<ListSubheader>Solicitação</ListSubheader>} className={classes.root}>
        {
            solicitado.map( (evento, index) => (
                <ListItemodal evento={evento} 
                            handlerConcluir={handlerConcluir} 
                            handlerEditar={handlerEditar} 
                            />
            ))
        }
        <Alert />
        <Backdro />
    </List>
  );
}

function ListItemodal({evento, handlerConcluir, handlerEditar}) {

    const [open, setOpen] = React.useState(false);
    const classes = useStyles();

    const handleOpen = () => {
        setOpen(true);
      };
    
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    id="acordion-nome"
                >
                    <ListItemAvatar>
                        {
                            evento.cliente.cliente?.avatar ?
                                <Avatar style={{ width: '40px', height: '40px' }}  src={evento.cliente.cliente?.avatar} >
                                    {evento.cliente?.first_name }
                                </Avatar>
                            :
                            <SubjectIcon />
                        }
                    </ListItemAvatar>
                    <ListItemText primary={`${evento.servico?.nome} - ${evento.cliente?.first_name }`} 
                                secondary={`${ moment(new Date(evento?.dia)).format("DD/MM/YYYY") } - ${ moment(new Date(evento?.dia)).format('H:mm') }`}  
                                />
                </AccordionSummary>
                <AccordionDetails>
                    <WrapperButton>
                        <Button
                                variant="contained"
                                color="primary"
                                className={classes.button}
                                startIcon={<EditIcon />}
                                onClick={handleOpen}

                            >
                            Editar
                        </Button>
                        <Button
                                variant="contained"
                                color="default"
                                className={classes.button}
                                onClick={(event) => handlerConcluir(evento)}
                                // endIcon={<Icon>send</Icon>}
                            >
                            Confirmar
                        </Button>
                    </WrapperButton>
                </AccordionDetails>

                <Modal
                        open={open}
                        onClose={handleClose}
                        className={classes.modal}
                        aria-labelledby="simple-modal-title"
                        aria-describedby="simple-modal-description"
                    >
                    <Edicao agendamento={evento} close={handleClose} editar={handlerEditar} />
                </Modal>

        </Accordion>
    )

}


export  { Solicitacao };