import React from 'react';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function useSnackbarsAlert() {

  const [open, setOpen] = React.useState(false);
  const [option, setOption] = React.useState({
    msg: "",
    severity: ""
  });

  const handleClick = (conf) => {
    setOption(conf)
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  const Componet = () => (
      <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
        <Alert onClose={handleClose} severity={option?.severity} >
            {option?.msg}
        </Alert>
      </Snackbar>
  );

  return [handleClick, handleClose, Componet]

}

function useBackdrop() {

  const [open, setOpen] = React.useState(false);

  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    if(!open){
      setOpen(!open);
    }
  };

  const Componet = () => (
      <Backdrop 
        style={{  
          zIndex: 10,
          color: '#fff'
        }} 
        open={open} 
        onClick={handleClose}
        >
        <CircularProgress color="inherit" />
      </Backdrop>
  );

  return [handleToggle, handleClose, Componet]

}


export { useSnackbarsAlert, useBackdrop }

